import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import { Container, Grid, Typography } from "@material-ui/core";
import { Button, LinearProgress } from "@material-ui/core";
import { PlayArrow, Pause, Stop } from "@material-ui/icons";

import TutorialThermometer from "../../Tutorials/TutorialThermometer/TutorialThermometer";
import TempContext from "../../../TempContext";

var toHHMMSS = (secs) => {
  if(secs) {
    var sec_num = parseInt(secs, 10)
    var hours   = Math.floor(sec_num / 3600)
    var minutes = Math.floor(sec_num / 60) % 60
    var seconds = sec_num % 60

    return [hours,minutes,seconds]
        .map(v => v < 10 ? "0" + v : v)
        .filter((v,i) => v !== "00" || i > 0)
        .join(":");
  } else {
    return "calculating...";
  }
}

const PrintStatus = ({
  lastMessage,
  progressState,
  setDisabledNav,
  state,
  statusText,
  fileName
}) => {
  const [paused, setPaused] = useState(false);
  const [value, setValue] = useState(0);
  const [elapsed, setElapsed] = useState("");
  const [remaining, setRemaining] = useState("");

  useEffect(() => {
    if (progressState.completion !== value) {
      setValue(progressState.completion);
      setElapsed(toHHMMSS(progressState.printTime));
      setRemaining(toHHMMSS(progressState.printTimeLeft));
      console.log(value);
    }
  }, [progressState, value]);

  return (
    <>
      <Container>
        <Typography
          variant="h5"
          style={{ marginTop: 35, width: "100%", textAlign: "center" }}
        >
          {statusText}: {fileName}
        </Typography>
        <div className="print-status-bar" style={{ marginTop: 25 }}>
          <BorderLinearProgress variant="determinate" value={value} />
          <Grid container spacing={3} alignItems="center">
          <Grid item xs={4}  style={{textAlign: "left"}}>
          <Typography
            variant="h6"
            component="span"
            style={{ width: "100%" }}
          >
            {elapsed}
          </Typography>
          </Grid>
          <Grid item xs={4}  style={{textAlign: "center"}}>
          <Typography
            variant="h6"
            component="span"
            style={{ width: "100%" }}
          >
            {value ? value.toFixed(1) : "0"}%
          </Typography>
          </Grid>
          <Grid item xs={4} style={{textAlign: "right"}}>
          <Typography
            variant="h6"
            component="span"
            style={{ width: "100%" }}
          >
            {remaining}
          </Typography>
          </Grid>
          </Grid>
        </div>
        <div style={{ marginTop: 50 }}>
          <TempContext.Consumer>
            {({ bed, nozzle, bedTarget, nozzleTarget }) => (
              <Grid container>
                <Grid item container xs={6} direction="column">
                  <Grid item style={{ marginBottom: -100 }}>
                    <div style={{ transform: "scale(.6)" }}>
                      <TutorialThermometer min={0} max={120} value={bed} />
                    </div>
                  </Grid>
                  <Grid item style={{ textAlign: "center" }}>
                    <h4>Target: {bedTarget}</h4>
                    <h4>Bed</h4>
                  </Grid>
                </Grid>
                <Grid item container xs={6} direction="column">
                  <Grid item style={{ marginBottom: -100 }}>
                    <div style={{ transform: "scale(.6)" }}>
                      <TutorialThermometer min={0} max={300} value={nozzle} />
                    </div>
                  </Grid>
                  <Grid item style={{ textAlign: "center" }}>
                    <h4>Target: {nozzleTarget}</h4>
                    <h4>Nozzle</h4>
                  </Grid>
                </Grid>
              </Grid>
            )}
          </TempContext.Consumer>
        </div>
        <div className="print-btn-container" style={{ marginTop: 50 }}>
          <Button
            color="primary"
            variant="contained"
            onClick={() => {
              setPaused((prevState) => !prevState);
              fetch(`http://localhost:2127/api/v1/job`, {
                method: "PATCH",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                  file: state.files[state.file]
                })
              });
            }}
            startIcon={!paused ? <Pause /> : <PlayArrow />}
          >
            {!paused ? "Pause" : "Play"}
          </Button>
          <Button
            color="secondary"
            variant="contained"
            onClick={() => {
              fetch(`http://localhost:2127/api/v1/job`, {
                method: "DELETE",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                  file: state.files[state.file]
                })
              });
            }}
            startIcon={<Stop />}
          >
            Stop
          </Button>
        </div>
      </Container>
    </>
  );
};

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 14,
    borderRadius: 7
  },
  colorPrimary: {
    backgroundColor: "#606060"
  },
  bar: {
    borderRadius: 7,
    backgroundColor: "#ACC12F"
  }
}))(LinearProgress);

export default PrintStatus;
